<template>
  <div>
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <region-city-store @StoreGroupChange="StoreGroupChange"></region-city-store>
        <el-form-item label="群接龙类型">
          <el-select 
            class="search-input" 
            v-model="searchContent.activityType" 
            filterable 
            placeholder="请选择类型" 
            clearable size="small" 
            @change="doSearchHandel(true)">
            <el-option 
              v-for="item in activityTypeOptions" 
              :key="item.value" 
              :label="item.label" 
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属团长店">
          <el-select class="search-input" v-model="searchContent.storeLeaderId" filterable placeholder="请选择所属团长店" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in storeLeaderOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="接龙活动ID">
          <el-input type="number" :controls="false" class="search-input" v-model.trim="searchContent.id" @keyup.enter.native="doSearchHandel(true)" placeholder="请输入接龙活动ID" size="small" clearable></el-input>
        </el-form-item>
        <el-form-item label="接龙时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRange"
            :clearable="false"
            :editable="false"
            :pickerOptions="{}"
            @datePickerRangeChange="datePickerRangeChange">
          </date-picker-range>
        </el-form-item>
        <el-form-item label="自提时间">
          <el-date-picker
            class="search-input"
            v-model="searchContent.pickUpTimeTemp"
            type="date"
            placeholder="请选择自提时间"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            size="small"
            @change="doSearchHandel(true)"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="状态">
          <el-select 
            class="search-input" 
            v-model="searchContent.activityStatus" 
            filterable 
            placeholder="请选择状态" 
            clearable size="small" 
            @change="doSearchHandel(true)">
            <el-option 
              v-for="item in activityStatusOptions" 
              :key="item.value" 
              :label="item.label" 
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-wraper">
      <el-table 
        class="fixed-height" 
        v-loading="loading" 
        :data="tableData" 
        empty-text="没有查询到您想要的数据" 
        style="width:100%;" 
        size="small">
        <el-table-column min-width="80px" prop="cityName" label="城市"></el-table-column>
        <el-table-column label="门店" min-width="130px">
          <template slot-scope="scope">
            <div>
              <p>{{scope.row.storeNo}}-{{scope.row.storeName}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="140px" label="团长店">
          <template slot-scope="scope" size="small">
            <p v-if="scope.row.storeLeaderId">{{scope.row.storeLeaderId}}_{{scope.row.virtualStoreName}}_{{scope.row.userName}}</p>
            <p v-else>-</p>
          </template>
        </el-table-column>
        <el-table-column min-width="80px" prop="id" label="群接龙ID"></el-table-column>
        <el-table-column min-width="130px" prop="activityName" label="群接龙名称"></el-table-column>
        <el-table-column min-width="170px" label="群接龙时间">
          <template slot-scope="scope" size="small">
            <div>
              <p>{{scope.row.activityStartTime}}</p>
              <p>至</p>
              <p>{{scope.row.activityEndTime}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="170px" prop="createTime" label="创建时间"></el-table-column>
        <el-table-column min-width="110px" prop="activityType" label="群接龙类型" :formatter="activityTypeFormatter"></el-table-column>
        <el-table-column min-width="110px" label="提货日期">
          <template slot-scope="scope" size="small">
            <div>
              <span v-if="scope.row.pickUpTime">{{scope.row.pickUpTime.substr(0, 10)}}</span>
              <span v-else>-</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="80px" prop="goodsNum" label="商品数"></el-table-column>
        <el-table-column min-width="90px" prop="solitairePersonNum" label="接龙人数"></el-table-column>
        <el-table-column min-width="90px" prop="totalOrderAmount" label="订单金额"></el-table-column>
        <el-table-column min-width="90px" prop="activityStatus" label="状态" :formatter="activityStatusFormatter"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="110">
          <template slot-scope="scope" size="small">
            <div>
              <p><el-button @click="showItem(scope.row)" size="mini" type="text" icon="el-icon-view">详情</el-button></p>
              <p><el-button @click="showOrderResult(scope.row)" size="mini" type="text" icon="el-icon-view">查看订单</el-button></p>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import config from '@/utils/config'
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore'
  import requestUrl from '@/api/Order'
  export default {
    data() {
      return {
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        pageSizes: config.pageSizes20,
        tableData: [],
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityId: '', // 城市id
          storeLeaderId: '',
          storeGroupTypeNo: '',
          storeGroupNo: '',
          activityStartTime: '', // 开始时间
          activityEndTime: '', // 结束时间
          defaultRange: [],
          activityStatus: '', // 活动状态：0-未开始，1-已发布，3-进行中，4-已结束，5-已终止
          pickUpTimeTemp: '',
          id: '',
          activityType: ''
        },
        storeLeaderOptions: [],
        activityStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '未开始',
            value: 2
          },
          {
            label: '已发布',
            value: 1
          },
          {
            label: '已终止',
            value: 5
          },
          {
            label: '进行中',
            value: 3
          },
          {
            label: '已结束',
            value: 4
          }
        ],
        activityTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '及时达群接龙',
            value: 2
          },
          {
            label: '预售群接龙',
            value: 1
          }
        ]

      }
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region-city-store": RegionCityStore
    },
    mounted() {
      // 获取所属团长店信息
      this.getStoreLeaderList()
      let searchContentMesg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMesg) {
        this.searchContent = searchContentMesg.searchContent
        this.pageNum = searchContentMesg.pageNum
        Storage.remove('searchContent')
      }
      this.doSearchHandel()
    },
    methods: {
      StoreGroupChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.searchContent.cityId = params.cityId
        this.searchContent.erpStoreNo = params.storeNo
        this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
        this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
        this.doSearchHandel(true)
      },
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.activityStartTime = result ? result[0] : ''
        this.searchContent.activityEndTime = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      getStoreLeaderList() {
        this.$http.post(requestUrl.getStoreLeaderList, {pageNum: 1, pageSize: -1, DIFFCITY: true}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.storeLeaderOptions = optionsArr.filter(item => {
              return item.applyStatus === 2 && item.userStatus === 0
            }).map(item => {
              return {
                id: item.id,
                applyStatus: item.applyStatus,
                virtualStoreName: item.virtualStoreName,
                userName: item.userName,
                actualStoreNo: item.actualStoreNo,
                value: item.id,
                label: `${item.id}_${item.virtualStoreName}_${item.userName}`
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取所属团长店信息):", e.message)
        })
      },
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      doSearchHandel(param) {
        if(param === true) {
          this.pageNum = 1
        }
        this.loading = true
        let params = {
          ...this.searchContent,
          pickUpTime: this.searchContent.pickUpTimeTemp ? `${this.searchContent.pickUpTimeTemp} 23:59:59` : ''
        }
        this.$http.post(requestUrl.listShopSolitaireActivity, { ...params, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              this.tableData = res.data && res.data.result && res.data.result.length ? res.data.result : []
              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      showItem(row) {
        let paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", paramMesg)
        if(row.storeLeaderId) {
          this.$router.push(`/solitaire_order/detail/${row.id}?storeNo=${row.storeNo}&storeName=${row.storeName}&storeLeaderId=${row.storeLeaderId}&virtualStoreName=${row.virtualStoreName}&userName=${row.userName}`)
        }else {
          this.$router.push(`/solitaire_order/detail/${row.id}?storeNo=${row.storeNo}&storeName=${row.storeName}`)
        }
      },
      activityStatusFormatter(row, column, cellValue, index) {
        let filterMsg = this.activityStatusOptions.filter(item => {
          return item.value === cellValue
        })[0]
        return filterMsg ? filterMsg.label : cellValue
      },
      activityTypeFormatter(row, column, cellValue, index) {
        let filterMsg = this.activityTypeOptions.filter(item => {
          return item.value === cellValue
        })[0]
        return filterMsg ? filterMsg.label : cellValue
      },
      showOrderResult(row) {
        let paramMsg = {
          from: 'solitaire',
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", paramMsg)
        let routerPath = `/search_result?from=solitaire&solitaireId=${row.id}`
        this.$router.push(routerPath)
      }
    },
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }
  }
</script>


